import React, { useEffect, useContext } from 'react';
import { authContext } from '../context/AuthProvider';
import { useQuery } from "react-query";
import AvatarPicker from '../components/AvatarPicker';
import ProfileInfoEditor from '../components/ProfileInfoEditor';

function Profile() {

  const [user, setUser] = useContext(authContext);
  const queryResult = useQuery("userdata", () => fetch("/api/profile").then(res => res.json()));

  useEffect(() => {
    if (queryResult.isSuccess) setUser({ ...user, ...queryResult.data.data });
  }, []);

  return (
    <main className="profile">
      {queryResult.isLoading ?
        <div className="loading">It is loading...</div>
        :
        queryResult.isError ?
          <div className="error">There was an error fetching. Please log out and try again</div>
          :
          <>
            <AvatarPicker {...{user, setUser}} />
            <div className="content">
              <h3 className="name">{user.username}</h3>
              <ProfileInfoEditor {...{ user, setUser }} />
              {/* <div style={{ display: "none" }} className="trophies">
                <div className="trophy speaker">M</div>
                <div className="trophy devil">M</div>
                <div className="trophy badass">M</div>
              </div> */}
            </div>
          </>
      }
    </main>
  )
}

export default Profile
