import React, { useEffect } from 'react'
import { useContext } from 'react';
import { useQuery } from 'react-query';
import { useHistory, useRouteMatch } from 'react-router-dom';
import { client } from '../../App';
import { authContext } from '../context/AuthProvider';

function Users() {

  const url = useRouteMatch();
  const { id } = url.params;
  const [user] = useContext(authContext);
  const history = useHistory();

  let anotherUser = useQuery("anotheruser", () => fetch("/api/users/" + id).then(res => res.json()), { cacheTime: 0 });

  return (
    <main className="profile user">
      {anotherUser.isLoading ?
        <div className="loading">It is loading...</div>
        :
        anotherUser.isError ?
          <div className="error">There was an error fetching. Please log out and try again</div>
          :
          !anotherUser.isFetching && anotherUser.data?.data ?
            <>

              <div className="avatar-section">
                <img className="image-picker" src={"/" + anotherUser.data.data.profile_image} alt="Profile" />
              </div>
              <div className="content">
                <h3 className="name">{anotherUser.data.data.username}</h3>
                <div className="info">
                  <p>{anotherUser.data.data.profile_info}</p>
                  {user.id &&
                    JSON.parse(anotherUser.data.data.friends).includes(user.id) ?
                    <button onClick={() => toggleFriend(anotherUser.data.data.id)} className="remove-friend"><i className="fas fa-user-times"></i></button>
                    :
                    <button onClick={() => toggleFriend(anotherUser.data.data.id)} className="add-friend"><i className="fas fa-user-plus"></i></button>
                  }
                </div>
              </div>
            </>
            :
            <></>
      }
    </main>
  )

  function toggleFriend(id){
    if(!user.id) history.push("/login");
    fetch("/api/friends", {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({ id })
    })
    .then(res => res.json())
    .then(result => {
      console.log(result);
      client.refetchQueries("anotheruser");
    })
    .catch(err => console.log(err))
  }
}

export default Users
