function openImagePicker() {
  document.querySelector(".image-picker-input").click();
}

function updateProfileImage(e, userImage){
  const image = e.target.files[0];
  if(image.type === "image/svg+xml") return; //Filter
  const blobImage = new Blob([image]);
  const url = URL.createObjectURL(blobImage);
  userImage.src = url;
}

export { openImagePicker, updateProfileImage };
