import React, { useState } from 'react'
import { client } from '../../App';

function ProfileInfoEditor({ user, setUser }) {

  const [edit, setEdit] = useState(false);
  const [profileInfo, setProfileInfo] = useState("");

  return (
    <div className="info">
      {!edit ?
        <>
          <p>{user.profile_info}</p>
          <button onClick={openEditMode} className="edit">Edit</button>
        </>
        :
        <>
          <textarea onChange={(e) => setProfileInfo(e.target.value)} className="set-personal-info" value={profileInfo} />
          <div className="actions">
            <button onClick={updateProfileInfo} className="set">Set</button>
            <button onClick={exitEditMode} className="cancel">Cancel</button>
          </div>
        </>
      }
    </div>
  )

  function exitEditMode() {
    setEdit(false);
  }

  function openEditMode() {
    setEdit(true);
    setProfileInfo(user.profile_info);
  }

  function updateProfileInfo() {
    fetch("/api/profile/info", {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({
        info: profileInfo
      })
    })
      .then(res => res.json())
      .then(result => {
        setUser({ ...user, profile_info: result.data });
        exitEditMode();
        client.removeQueries();
      })
      .catch(err => {
        if (err) {
          console.log(err);
        }
      })
  }
}

export default ProfileInfoEditor
