import React, { useContext } from 'react'
import { useQuery } from 'react-query';
import { Link } from "react-router-dom"
import { client } from '../../App';
import { authContext } from '../context/AuthProvider';

function TopicAll() {

  const [user] = useContext(authContext);
  const topics = useQuery("topics", () => fetch("/api/topics").then(res => res.json()));
  console.log(topics);

  return (
    <>
      {
        user.logged &&
        <section className="create-topic">
          <Link to="/topics/create" className="add-topic">Add a new topic!</Link>
        </section>
      }
      < main className="topics" >
        {
          topics.isLoading ?
            <div> Loading... </div>
            :
            topics.isError ?
              <div> There was an error, please refresh </div>
              :
              <>
                {topics.data.data.map(topic =>
                  <article key={topic.id} className={`topic ${topic.id}`}>
                    <Link to={`/topics/${topic.id}`} >
                      <div className="rating">{
                        [1, 2, 3, 4, 5].map(rate => rate <= topic.rating ? <i key={rate} className="fas fa-star"></i> : <i key={rate + 100} className="far fa-star"></i>)
                      }</div>
                      <h3 className="title">{topic.subject[0].toUpperCase() + topic.subject.slice(1)}</h3>
                      <p className="content">
                        {topic.intro}
                      </p>
                      <h4 className="founder">{topic.authorName}</h4>
                    </Link>
                    {topic.author === user.id && <button onClick={() => deleteTopic(topic.id)} className="delete"><i className="fa fa-trash" aria-hidden="true" /></button>}
                  </article>
                )}
              </>
        }
      </main >
    </>
  )

  function deleteTopic(id) {
    fetch("/api/topics", {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({ id })
    })
      .then(res => res.json())
      .then(result => {
        console.log(result);
        client.refetchQueries("topics");
      })
      .catch(err => console.log(err))
  }
}

export default TopicAll
