import React from "react";

function Home() {
  return (
    <>
      <main className="home">
        <div className="content">
          <h2 className="title">Prepare to discuss</h2>
          <p className="intro">
            Este sitio fue ideado con la premisa de que a todos les gusta hablar mal de algo.
            Y debido a lo comun que es, he considerado necesario declararlo como un Derecho Humano,
            el derecho de quejarse de todo. Por lo anterior, esta plataforma simplemente se presenta
            como un medio de descarga emocional, y... por que no (en el mejor de los casos) como punto
            de discusiones constructivas y tertulias amenas.
          </p>
          <div className="random">
            <span>Random Discussion</span><button className="go-to-comments">Join</button>
          </div>
        </div>
      </main>
      {/* <section className="hottest-topics">
        <article className="topic">Games</article>
        <article className="topic">War</article>
        <article className="topic">Heroes</article>
        <article className="topic">Religion</article>
      </section> */}
    </>
  )
}

export default Home;