import React, { useContext, useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom';
import { authContext } from "../context/AuthProvider"
import checkLoginToRedirect from '../logic/checkLoginToRedirect';

function Register() {

  const history = useHistory();
  const [ user ] = useContext( authContext );
  const [ wrong, setWrong ] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  useEffect(() => {
    checkLoginToRedirect(user, history);
  }, []);

  return (
    <main className="register-form">
      <h3 className="title">Register</h3>
      <form>
        <input onChange={(e) => setUsername(e.target.value)} type="text" className={`username ${wrong === "username" && "error-primary"}`} placeholder="Username" value={username}/>
        <input onChange={(e) => setPassword(e.target.value)} type="password" className={`password ${wrong === "password" && "error-primary"}`} placeholder="Password"  value={password}/>
        <button onClick={register} className="register">Register</button>
      </form>
    </main>
  )

  function register(e) {
    e.preventDefault();
    fetch("api/register", {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      method: "POST",
      body: JSON.stringify({username, password})}
    )
    .then(res => res.json())
    .then(result => {
      console.log(result);
      if(!result.error){
        history.replace("/login");
        return;
      }
      if(result.cause === "username"){
        setWrong("username");
      }
      if(result.cause === "password"){
        setWrong("password");
      }
    })
    .catch(err => {
      console.log(err)
      if(err) console.log("No se pudo enviar la data...")
    })
  }
}


export default Register
