import React, { useContext } from "react";
import { NavLink, Link, useLocation, useHistory } from "react-router-dom";
import { client } from "../../App";
import { authContext } from "../context/AuthProvider";
import SearchBar from "./SearchBar";

function Header() {

  const [user, setUser] = useContext(authContext);
  const { pathname: path } = useLocation();
  const history = useHistory();

  return (
    <header className="main-header">
      {path.indexOf("/login") !== 0 && (user.logged ?
        <button onClick={logout} className="logout">Log Out</button>
        :
        <Link to="/login" className="login">Log In</Link>)
      }
      <h1 className="title">COMMENTS APP</h1>
      <div className="comment">
      <SearchBar className="add add-main" placeholder="Look for a topic!" />
      </div>
      <nav>
        <ul>
          <NavLink activeClassName="here" exact to="/">Home</NavLink>
          <NavLink activeClassName="here" to="/profile">Profile</NavLink>
          <NavLink activeClassName="here" to="/topics">Topics</NavLink>
          <NavLink activeClassName="here" to="/enemies">Enemies</NavLink>
        </ul>
      </nav>
    </header>
  )

  function logout() {
    fetch("/api/logout", {
      method: "POST"
    })
      .then(res => res.json())
      .then(result => {
        setUser({ logged: false });
        console.log(result);
        client.removeQueries();
        history.push("/")
      })
      .catch(err => { if(err) console.log(err) })
  }
}

export default Header;