import React, { useContext, useEffect, useState } from 'react'
import { useHistory, useLocation, Link } from 'react-router-dom';
import { authContext } from "../context/AuthProvider"
import checkLoginToRedirect from '../logic/checkLoginToRedirect';

function Login() {

  const [user, setUser] = useContext(authContext);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [ wrong, setWrong ] = useState("");
  const { from } = useLocation().state || { from: { pathname: "/" } };
  const history = useHistory();
  useEffect(() => {
    checkLoginToRedirect(user, history);
  }, []);

  return (
    <main className="login-form">
      <h3 className="title">Login</h3>
      <form>
        <input onChange={(e) => setUsername(e.target.value)} value={username} type="text" className={`username ${wrong === "username" && "error-primary"}`} placeholder="Username" />
        <input onChange={(e) => setPassword(e.target.value)} value={password} type="password" className={`password ${wrong === "password" && "error-primary"}`} placeholder="Password" />
        <button onClick={login} className="login">Login</button>
      </form>
      <div className="register-now">      
        <span> Have not an account yet? </span><Link to="/register">Sign In</Link>
      </div>
    </main>
  )

  function login(e) {
    e.preventDefault();
    fetch("api/login", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({ username, password })
    })
      .then(res => res.json())
      .then(userData => {
        if (!userData.error) {
          setUser({ ...userData.data, logged: true });
          console.log(userData);
          history.replace(from);
          return
        }
        if(userData.cause === "username"){
          setWrong("username");
        }
        if(userData.cause === "password"){
          setWrong("password");
        }
      })
      .catch(err => { if (err) console.log(err) })
  }
}


export default Login
