import React from 'react'
import { useContext } from 'react';
import { Link } from "react-router-dom";
import { useQuery } from 'react-query';
import { authContext } from '../context/AuthProvider';

function Enemies() {

  const [user] = useContext(authContext);
  const queryResult = useQuery("enemies", () => fetch("/api/friends").then(res => res.json()), { enabled: !!user.id });

  return (
    <main className="enemies">
      <h2 className="title">Your enemies</h2>
      {
        !user.id ?
          <article className="no-friends">
            <p>You should login to see your friends</p>
            <Link to="/login" className="login">Login</Link>
          </article>
          : queryResult.isFetching ?
            <div className="loading">It is loading...</div>
            :
            queryResult.isError ?
              <div className="error">There was an error, please refresh</div>
              :
              !queryResult.data?.data.length && user.id ?
                <article className="no-friends">
                  There are no friends to show, man.
                </article>
                :
                queryResult.data.data.map(friend => (
                  <Link to={`/users/${friend.id}`} key={friend.id} className="friend">
                    <img src={friend.profile_image} alt={friend.username} className="avatar" />
                    <div className="info">
                      <h3 className="username">{friend.username}</h3>
                      <p className="info">{friend.profile_info}</p>
                    </div>
                  </Link>
                ))
      }
    </main>
  )
}

export default Enemies
