import React, { useContext, useState } from 'react'
import { useHistory } from 'react-router-dom';
import { client } from '../../App';
import { searchContext } from '../context/SearchProvider';

function SearchBar({ placeholder, className }) {

  const [ search, setSearch ] = useState("");
  const [ globalSearch, setGlobalSearch ] = useContext(searchContext);
  const history = useHistory();

  return (
    <form>
      <input value={search} onChange={ e => setSearch(e.target.value)} type="text" {...{placeholder}} />
      <button onClick={searchForTopic} {...{className}}><i className="fas fa-search"></i></button>
    </form>
  )
  function searchForTopic(e){
    e.preventDefault();
    if(!(search.length)) return;
    history.push("/topics/search/" + search);
    setGlobalSearch(search);
    setSearch("");
  }
}

export default SearchBar
