import React, { useContext, useEffect, useRef, useState } from 'react'
import { useHistory, Link } from 'react-router-dom';
import { authContext } from '../context/AuthProvider';

function Comment({ source }) {

  const [commentMode, setCommentMode] = useState(false);
  const [showChildComments, setShowChildComments] = useState(false);
  const [answerContent, setAnswerContent] = useState("");
  const [user] = useContext(authContext);
  const [childComments, setChildComments] = useState(JSON.parse(source.children));
  const { commenting, setCommenting } = source;
  const history = useHistory();

  const ref = useRef();


  useEffect(() => {
    document.activeElement.blur();
    if(ref.current) ref.current.focus();
  }, [commentMode])

  return (
    <article className={`comment ${source.id}`}>
      <Link className="author" to={source.author === user.id ? "/profile" : `/users/${source.author}`}>{source.authorName}</Link>
      <p className="content">
        {source.content}
      </p>
      <ul className="actions">
        {commentMode ? 
          <>
            <input ref={ref} value={answerContent} onChange={e => setAnswerContent(e.target.value)} type="text" className="answer-comment" />
            <button onClick={addChild} className="send-answer-comment">Send</button>
            <button onClick={() => { 
              setCommentMode(false);
              setCommenting(false); 
              }} className="abort-answer-comment">&times;</button>
          </>
          :
          <>
            {!!childComments.length &&
              <button onClick={toggleChildCommentsVisibility} className="see-more">...</button>
            }
            <button onClick={() => { 
              if(commenting) return;
              if(!user.logged) history.push("/login");
              setCommentMode(true);
              setCommenting(true); 
              }} className="answer">+</button>
          </>
        }
        {user.id === source.author && <button onClick={(e) => deleteComment(e, source.id)} className="delete"><i className="fa fa-trash" aria-hidden="true"></i></button>}
      </ul>
      <ol className="children">
        {!!childComments.length && showChildComments &&
          childComments.map(child => <Comment key={child.id + 1000000000} source={{ ...child, childComments, setChildComments, commenting, setCommenting }} />)
        }
      </ol>
    </article>
  )

  function deleteComment(e, id) {
    
    source.setChildComments(source.childComments.filter(comment => comment.id !== id));

    fetch("/api/comment", {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({ id })
    })
      .then(res => res.json())
      .then(result => console.log(result))
      .catch(err => { if (err) console.log(err) })
      .finally(() => { if(!document.querySelector(".answer-comment")) setCommenting(false) } )
  }

  function addChild(e) {
    setCommentMode(false);
    setCommenting(false);
    setAnswerContent("");
    fetch("/api/comment/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({
        topic: source.topic,
        content: answerContent,
        parent: source.id
      })
    })
      .then(res => res.json())
      .then(result => {
        setChildComments([ ...childComments, result.data ]);
        if(!showChildComments) toggleChildCommentsVisibility();
      })
      .catch(err => { if (err) console.log(err) })
  }

  function toggleChildCommentsVisibility() {
    if (showChildComments === false) {
      fetch(`/api/comment/${source.id}`)
        .then(res => res.json())
        .then(result => setChildComments(result))
        .catch(err => console.log(err))
        .finally(() => setShowChildComments(true))
        return;
    }
    setShowChildComments(false);
  }
}

export default Comment
