import React, { createContext, useState, useEffect } from 'react';

export const authContext = createContext();

function AuthProvider({ children }) {
  let hasSiteCookie = false;
  for (const cookiePair of document.cookie.split(";")) {
    if (cookiePair.indexOf("connect.sid") === 0) hasSiteCookie = true;
  }
  const [user, setUser] = useState({ logged: hasSiteCookie });

  useEffect(() => {
    if(user.logged){
      fetch("/api/profile")
      .then(res => res.json())
      .then(result => setUser({logged: true, ...result.data}))
      .catch(err => { if (err) console.log(err) })
    }
  }, [])

  return (
    <authContext.Provider value={[user, setUser]}>
      {children}
    </authContext.Provider>
  )
}

export default AuthProvider;
