import React, { useEffect, useState, useContext } from 'react';
import { useHistory, useLocation } from 'react-router';
import Comment from '../components/Comment';
import { authContext } from '../context/AuthProvider';

function Topic() {

  const location = useLocation();
  const { pathname } = location;
  const [user] = useContext(authContext);
  const [wrong, setWrong] = useState("");
  let [topic, setTopic] = useState("");
  const history = useHistory();
  let [childComments, setChildComments] = useState([]);
  const [commenting, setCommenting] = useState(false);
  const [mainComment, setMainComment] = useState("");
  useEffect(() => {
    console.log("/api" + pathname);
    fetch("/api" + pathname)
      .then(res => res.json())
      .then(result => {
        const { topic: resTopic, comments: resComments } = result;
        setTopic(resTopic);
        setChildComments(resComments);
        console.log(resTopic);
      })
      .catch(err => { if (err) console.log(err) })
  }, [])

  return (
    <>
    {
      (topic.subject && topic.intro) ?
      <>
        <main className="topic">
          <h3 className="title">{topic.subject[0].toUpperCase() + topic.subject.slice(1)}</h3>
          <p className="intro">{topic.intro}</p>
        </main>
        <article className="actions">
          <input type="text" className={`comment-content ${wrong === "main" && "error-primary"}`} onChange={(e) => setMainComment(e.target.value)} value={mainComment} />
          <button className="add-comment" onClick={addMainComment}>+</button>
        </article>
        <section className="comments">
          {childComments.map(comment => <Comment key={comment.id + 10000000} source={{ ...comment, childComments, setChildComments, commenting, setCommenting }} />
          )
          }
        </section>
      </>
      :
      <p>Loading...</p>
    }
    </>
  )

function addMainComment() {

  if (!user.logged) history.push("/login");

  if (!(mainComment.length)) {
    setWrong("main");
    return;
  }

  setWrong("");

  fetch("/api/comment", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "Accept": "application/json"
    },
    body: JSON.stringify({
      topic: topic.id,
      parent: null,
      content: mainComment
    })
  })
    .then(res => res.json())
    .then(result => {
      setMainComment("");
      setChildComments([...childComments, { ...result.data, authorName: user.username }]);
    })
    .catch(err => console.log(err))
}
}

export default Topic
