import React from 'react'
import { Route, Switch } from 'react-router-dom';
import Topic from './Topic';
import TopicAll from './TopicAll';
import TopicCreate from './TopicCreate';
import TopicSearched from './TopicSearched';

function Topics() {
  return (
    <Switch>
      <Route exact path="/topics" component={TopicAll} />
      <Route path="/topics/create" component={TopicCreate} />
      <Route exact path="/topics/search/:search" component={TopicSearched} />
      <Route exact path="/topics/:id" component={Topic} />
    </Switch>
  )
}

export default Topics
