function checkLoginToRedirect(user, history) {
  if (user.id) return;
  fetch("api/profile", {
    method: "GET"
  })
    .then(res => res.json())
    .then(user => {
      console.log(user);
      if (user.error) return;
      if (!(user.id + 100)) return;
      history.replace("/");
    })
}

export default checkLoginToRedirect