import React, { useState } from 'react';
import { useHistory } from "react-router-dom";

function TopicCreate() {

  const [ subject, setSubject ] = useState("");
  const [ intro, setIntro ] = useState("");
  const history = useHistory();
 
  return (
    <main className="create-topic">
      <h3 className="title">What topic would you like to create?</h3>
      <form className="topic-info">
        <input placeholder="Topic's title" type="text" className="subject" value={subject} onChange={ e => setSubject(e.target.value)}/>
        <textarea placeholder="A small introduction..." className="intro" value={intro} onChange={ e => setIntro(e.target.value)}></textarea>
        <button className="create" onClick={sendNewTopic}>Create!</button>
      </form>
    </main>
  )

  function sendNewTopic(e){
    e.preventDefault();
    if(!subject.length || !intro.length) return;
    const formattedIntro = formatText(intro);
    const topic = { subject, intro: formattedIntro };
    fetch("/api/topics", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({ ...topic })
    })
    .then(res => res.json())
    .then(result => {
      console.log(result);   
      setIntro("");
      setSubject("");
      history.push("/topics");
    })
    .catch(err => console.log(err))
  }

  function formatText(text){
    return text.charAt(0).toUpperCase() + text.slice(1);
  }
}

export default TopicCreate
